<template>
    <div class="DrawerMessage1">
      <div>
        <div class="fiexoverTitle mb36 mt24"  @click="SelectShow = true">
          <div class="fiexover">
            <p>
              开票资料
            </p>
            <div class="cl_333">
              {{SelectText}}
              <i class="iconfont"></i>
            </div>
          </div>
        </div>
        <van-collapse v-model="activeNames">
          <van-collapse-item title="税号" :name=index  :value=data.text class="mb24" v-for="(data,index) in TallageData" :key="index">
            <div class="species">
              <div  class="lefts">
                <p >
                  <span>单位名称：</span>
                  <span>{{data.danwei}}</span>
                </p>
                <p >
                  <span>开户行 ：</span>
                  <span>{{data.kaihuhang}}</span>
                </p>
                <p >
                  <span>账号 ：</span>
                  <span>{{data.zhanghao}}</span>
                </p>
                <p >
                  <span>地址：</span>
                  <span>{{data.dizhi}}</span>
                </p>
                <p >
                  <span>电话：</span>
                  <span>{{data.dianhua}}</span>
                </p>
              </div>
              <div  class="rights">
                <p class="delete" @click="removeIndex=index,obtainshow=true">删除</p>
                <p class="compile"  @click="compile(data,index)">编辑</p>
              </div>
            </div>
          </van-collapse-item>
        </van-collapse>
      </div>
      <!--    确认删除发票信息弹窗-->
      <van-dialog v-model="obtainshow"  show-cancel-button   class="windows" confirmButtonColor="#f90" confirmButtonText="是" cancelButtonText="否" @confirm="RemoveBall">
        <p >是否确认已收到货？</p>
      </van-dialog>
      <van-action-sheet v-model="SelectShow" :actions="actions" @select="onSelect" :round=false @open="changeColor"/>
    </div>
</template>
<script>
    import Vue from "vue"

    export default {
        name: "DrawerMessage1",
        components: {},
        data() {
            return {
              activeNames: ['1'],
              //确认删除发票信息弹窗
              obtainshow:false,
              //要删除的对象下标
              removeIndex:"",

              //开票资料
              SelectShow: false,
              actions: [
                {name: '增值税普通发票',color:""},
                { name: '别的也不知道了',color:""},
              ],
              SelectText:"增值税普通发票",
              //税号总数据
              TallageData1:[
                {name:true,text:"aaaaaa",danwei:"捷配1",kaihuhang:"招商银行1",zhanghao:"17637408019",dizhi:'瞎胡层序亲天禧1',dianhua:"17637408019"},
              ],
              TallageData:[],
              TallageData2:[
                {name:true,text:"bbbbb",danwei:"捷配1",kaihuhang:"招商银行1",zhanghao:"17637408019",dizhi:'瞎胡层序亲天禧1',dianhua:"17637408019"},
              ],
            }
        },
        methods: {
          onSelect(item) {
            // 默认情况下点击选项时不会自动收起
            // 可以通过 close-on-click-action 属性开启自动收起
            this.SelectShow = false;
            this.SelectText = item.name;
            this.appreciation(this.SelectText);
          },
          changeColor(){
            this.actions.forEach((item,index)=>{
              console.log(item);
              item.color="";
              if(item.name==this.SelectText){
                item.color="#f90"
              }
            });


            console.log("1");
          },
          //点击编辑
          compile(data,i){
            this.$router.push({
              name:"EditInformation",
              params:{index:i,data:data,SelectText:this.SelectText,id:2}
            })
          },
          //删除弹窗点击确定
          RemoveBall(){
            let i=this.removeIndex;
            this.SelectText=="增值税普通发票"?this.TallageData1.splice(i,1): this.TallageData2.splice(i,1);
          },
          //判断是增值税发票的函数
          appreciation(data){
            console.log(data);
            if(data=="增值税普通发票"){
              this.TallageData=this.TallageData1
            }else{
              this.TallageData=this.TallageData2
            }
          },
        },
        created() {
          //判断是否为传递过来的数值,是传递过来的数值后在进行判断
          console.log(this.$store.state.vuex.PlusTicketHome.id);
          //如果等于1说明是添加过来的
          if(this.$store.state.vuex.PlusTicketHome.id==1){
            //再判断是添加的增值税还是普通发票
            if(this.$store.state.vuex.PlusTicketHome.SelectText=="增值税普通发票"){
              this.TallageData1.push(this.$store.state.vuex.PlusTicketHome);
              this.TallageData=this.TallageData1
            }else{
              this.TallageData2.push(this.$store.state.vuex.PlusTicketHome);
              this.TallageData=this.TallageData2
            }
          }else{
            //先判断是增值税还是普通发票咯
            if(this.$store.state.vuex.PlusTicketHome.SelectText=="增值税普通发票"){
              this.TallageData1[this.$store.state.vuex.PlusTicketHome.OnIndex]=this.$store.state.vuex.PlusTicketHome
              this.TallageData=this.TallageData1
            }else{
            }
          }
          this.appreciation(this.SelectText);
        }
    }
</script>
<style  lang="less" scoped>
  .species{
    .lefts{background-color: #ffffff;
    p{display:flex;    justify-content: space-between;margin-bottom: .2rem}
    }
    .rights{
      font-size: .28rem;text-align: right;padding-top: .2rem;height: 1.02rem;
      .delete{height: .62rem;line-height: .62rem;border: 1px solid #d1d3d8;width: 1.39rem;color: #d1d3d8;text-align: center;display: inline-block}
      .compile{height: .62rem;line-height: .62rem;border: 1px solid #f90;width: 1.39rem;color: #ff9900;text-align: center;display: inline-block}
    }
  }
  .windows{
    width: 5.4rem;
    div{
      /*padding-left: .3rem;padding-right: .3rem;*/
    }
    p{margin-bottom: .3rem;padding-left: .3rem}
  }
</style>



